<template>
  <transition name="fade">
    <div class="modal_custom">
      <div class="modal-dialog" role="document">
        <!--模态框内容-->
        <div class="modal-content">
          <!--模态框内容-头部-->
          <div class="modal-header">
            <button type="button" class="close" aria-label="Close" @click="modelDialogClose"><span
              aria-hidden="true">&times;</span></button>
            <h4 class="modal-title">{{modalTitle}}</h4>
          </div>
          <!--模态框内容-主体-->
          <slot class="modal-dialog" name="modalBody"></slot>
          <!--模态框内容-底部-->
          <div class="modal-footer">
            <button type="button" class="btn btn-default" @click="modalCloseClick">关闭</button>
            <button type="button" class="btn btn-primary" @click="modalSureClick">确定</button>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
  export default {
    name: 'ModelDialog',
    props: {
      title: String,
      closeClick: Function,
      sureClick: Function
    },
    data() {
      return {
        modalTitle: this.title
      }
    },
    methods: {
      modelDialogClose(){
        this.closeClick()
      },
      modalCloseClick() {
        this.closeClick()
      },
      modalSureClick() {
        this.sureClick()
      }
    }
  }
</script>

<style scoped>
  .modal_custom {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1050;
    /*overflow: hidden;*/
    -webkit-overflow-scrolling: touch;
    outline: 0;
    /*overflow: auto;*/
    overflow-scrolling: auto;
    /*max-height: 100%;*/

  }

  /* 可以设置不同的进入和离开动画,设置持续时间和动画函数 */
  .fade-enter-active, .fade-leave-active {
    transition: opacity 700ms;
  }

  /* .fade-leave-active, 2.1.8 版本以下 */
  .fade-enter, .fade-leave-to {
    opacity: 0
  }
</style>



