<template>
  <div class="top15">
    <div>
      <span class="h3">{{personalInfo.object.tenantName}}</span>
      <button type="button" class="btn btn-default pull-right" @click="updatePersonalInfoClick">修改个人信息</button>
    </div>
    <p class="top15">状态: <span class="text-info">{{personalInfo.object.status===1?'开放':'禁用'}}</span></p>
    <p>公司: {{personalInfo.object.company}}</p>
    <p>号码: {{personalInfo.object.phone}}</p>
    <p>邮箱: {{personalInfo.object.email}}</p>
    <p>角色: {{personalInfo.object.roleNames}}</p>
    <p>权限: {{personalInfo.object.permissionNames}}</p>
    <p>创建时间: {{personalInfo.object.createDate}}</p>
    <p>更新时间: {{personalInfo.object.updateDate}}</p>
    <table class="table table-striped table-bordered table-hover text-center top15">
      <thead>
      <tr>
        <th class="text-center">名称</th>
        <th class="text-center">描述</th>
        <th class="text-center">类型</th>
        <th class="text-center">权限</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(item, index) in personalInfo.object.roleInfos" :key="index">
        <td>{{item.roleName}}</td>
        <td>{{item.roleDesc}}</td>
        <td>{{item.roleType}}</td>
        <td>

          <table class="table table-striped table-bordered table-hover text-center">
            <thead>
            <tr>
              <th class="text-center">名称</th>
              <th class="text-center">描述</th>
              <th class="text-center">类型</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(item1, index1) in item.permissionInfos" :key="index1">
              <td>{{item1.permissionName}}</td>
              <td>{{item1.permissionDesc}}</td>
              <td>{{item1.permissionType}}</td>
            </tr>
            </tbody>
          </table>

        </td>
      </tr>
      </tbody>
    </table>


    <ModalDialog title="个人信息修改" :close-click="personalUpdateModalCloseClick"
                 :sure-click="personalUpdateModalSureClick" v-show="personalUpdateModalDialogShow">
      <div slot="modalBody">
        <div class="form-group top15">
          <div class="row">
            <label class="col-sm-2 text-center">名称</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="tenant name (长度6~16)"
                     v-model="personalUpdateInfo.tenantName">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">密码</label>
            <div class="col-sm-9">
              <input type="password" class="form-control" placeholder="tenant password (长度8~24)"
                     v-model="personalUpdateInfo.password">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">确认密码</label>
            <div class="col-sm-9">
              <input type="password" class="form-control" placeholder="tenant password (长度8~24)"
                     v-model="personalUpdateInfo.surePassword">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">邮箱</label>
            <div class="col-sm-9">
              <input type="email" class="form-control" placeholder="tenant email" v-model="personalUpdateInfo.email">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">电话</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="tenant phone" v-model="personalUpdateInfo.phone">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">头像</label>
            <div class="col-sm-9">
              <input type="file" class="file-upload" @change="selectUpdateLogo">
            </div>
          </div>
        </div>
      </div>
    </ModalDialog>

  </div>
</template>

<script>

  import ModalDialog from './comment/ModelDialog'

  export default {
    name: 'TortoisePersonal',
    components: {
      ModalDialog
    },
    data () {
      return {
        personalInfo: null,
        personalUpdateInfo: {
          tenantName: '',
          password: '',
          surePassword: '',
          email: null,
          phone: null,
          headFile: null
        },
        personalUpdateModalDialogShow: false,
        //验证条件
        validatePass: {
          surePassword: false,
          headFile: false
        }
      }
    },
    created () {
      this.personalInfo = this.$global.getAuthInfo()
    },
    methods: {
      //修改个人信息
      updatePersonalInfoClick () {
        this.personalUpdateModalDialogShow = true
      },
      personalUpdateModalCloseClick () {
        this.personalUpdateModalDialogShow = false
      },
      personalUpdateModalSureClick () {
        this.personalUpdateModalDialogShow = false
        let formData = new FormData()
        if (this.personalUpdateInfo.tenantName.length > 0) {
          if (this.personalUpdateInfo.tenantName.length > 5) {
            formData.append('tenantName', this.personalUpdateInfo.tenantName)
          } else {
            this.$pubSub.publish('toastShow', {
              type: 'warning',
              msg: '租户名称大于5位'
            })
          }
        }
        if (this.personalUpdateInfo.password.length > 0) {
          if (this.personalUpdateInfo.password.length > 7 && this.validatePass.surePassword) {
            formData.append('password', this.personalUpdateInfo.password)
          } else {
            this.$pubSub.publish('toastShow', {
              type: 'warning',
              msg: '密码长度大于7位或两次输入密码不一致'
            })
          }
        }
        if (this.personalUpdateInfo.email) {
          formData.append('email', this.personalUpdateInfo.email)
        }
        if (this.personalUpdateInfo.phone) {
          formData.append('phone', this.personalUpdateInfo.phone)
        }
        if (this.validatePass.headFile) {
          formData.append('headFile', this.personalUpdateInfo.headFile)
        }
        this.$axios.put(this.$global.url.tenant.updatePersonal, formData)
          .then(response => {
            if (response !== null) {
              let data = response.data.data
              //更新当前界面
              if (data.tenantName != null) {
                this.personalInfo.object.tenantName = data.tenantName
              }
              if (data.email != null) {
                this.personalInfo.object.email = data.email
              }
              if (data.phone != null) {
                this.personalInfo.object.phone = data.phone
              }
              if (data.headUrl != null) {
                this.personalInfo.object.headUrl = data.headUrl
              }
              //重缓存个人信息
              sessionStorage.setItem(this.$global.cacheKey.authInfoKey, JSON.stringify(this.personalInfo))
              //提示
              this.$pubSub.publish('toastShow', {
                type: 'success',
                msg: '修改个人信息成功'
              })
            }
          })
      },
      selectUpdateLogo (event) {
        this.personalUpdateInfo.headFile = event.target.files[0]
        this.validatePass.headFile = /\.(png|jpg|gif)$/i.test(this.personalUpdateInfo.headFile.name) && this.personalUpdateInfo.headFile.size < 1024 * 2048
      }
    }
    ,
    watch: { //监视
      'personalUpdateInfo.surePassword':
        {
          deep: true,
          handler:

            function (newValue, oldValue) {
              this.validatePass.surePassword = newValue === this.personalUpdateInfo.password
            }
        }
      ,
      'personalUpdateInfo.password':
        {
          deep: true,
          handler:

            function (newValue, oldValue) {
              this.validatePass.surePassword = newValue === this.personalUpdateInfo.surePassword
            }
        }
    }
  }
</script>

<style scoped>

</style>



