<template>
  <div class="topNav">

    <button type="button" class="btn btn-sm btn-success" v-show="isAdmin" @click="addTenantClick">新增租户</button>
    <table class="table table-striped table-bordered table-hover text-center top15">
      <thead>
      <tr>
        <th class="text-center">公司</th>
        <th class="text-center">账号</th>
        <th class="text-center">状态</th>
        <th class="text-center">更新时间</th>
        <th class="text-center">操作</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(item, index) in tenantInfos" :key="index" @click="delegateAppItemClick($event,item,index)">
        <td>{{item.company}}</td>
        <td>{{item.tenantName}}</td>
        <td>{{item.status===1?'开放':'禁用'}}</td>
        <td>{{item.updateDate}}</td>
        <td class="text-center">
          <button type="button" class="btn btn-xs btn-danger" v-show="isAdmin">{{item.status===1?'开放':'禁用'}}</button>
          <button type="button" class="btn btn-xs btn-info">详情</button>
          <button type="button" class="btn btn-xs btn-primary" v-show="isAdmin">修改</button>
        </td>
      </tr>
      </tbody>
    </table>

    <!--
    点击---租户添加
     -->
    <ModalDialog title="租户添加" :close-click="addTenantModalCloseClick"
                 :sure-click="addTenantModalSureClick" v-show="tenantAddModalDialogShow">
      <div slot="modalBody">
        <div class="form-group top15">
          <div class="row">
            <label class="col-sm-2 text-center">账号</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="tenant name (长度6~16)"
                     v-model="tenantInfo.tenantName">
            </div>
            <span class="text-info small">必填</span>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">密码</label>
            <div class="col-sm-9">
              <input type="password" class="form-control" placeholder="tenant password (长度8~24)"
                     v-model="tenantInfo.password" :disabled="!validatePass.tenantName">
            </div>
            <span class="text-info small">必填</span>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">公司</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="tenant company (长度2~32)" v-model="tenantInfo.company"
                     :disabled="!validatePass.password">
            </div>
            <span class="text-info small">必填</span>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">邮箱</label>
            <div class="col-sm-9">
              <input type="email" class="form-control" placeholder="tenant email" v-model="tenantInfo.email">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">电话</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="tenant phone" v-model="tenantInfo.phone">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">头像</label>
            <div class="col-sm-9">
              <input type="file" class="file-upload" @change="selectLogo">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">状态</label>
            <div class="col-sm-9">
              <select class="form-control" v-model="tenantInfo.statusValue">
                <option>禁用</option>
                <option>开放</option>
              </select>
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">角色</label>
            <div class="col-sm-9">
              <label class="checkbox-inline" v-for="(item, index) in adminRoleInfos" :key="index">
                <input type="checkbox" :disabled="item.id<3" :value="item.id"
                       v-model="checkTenantRoleIds"> {{item.roleName}}
              </label>
            </div>
          </div>
        </div>
      </div>
    </ModalDialog>

    <!--
    点击---租户详情
    -->
    <ModalDialog title="租户详情" :close-click="tenantDetailModalCloseClick"
                 :sure-click="tenantDetailModalSureClick" v-show="tenantDetailModalDialogShow">
      <div slot="modalBody">
        <div class="row">
          <img :src="$global.url.base+tenantDetail.headUrl" class="text-center img-circle col-sm-3"
               style="margin-top: 10px;min-height: 80px;max-height: 300px">
          <div class="col-sm-9">
            <h3 class="h3">{{tenantDetail.tenantName}}</h3>
            <p>状态: <span class="text-info">{{tenantDetail.status===1?'开放':'禁用'}}</span></p>
            <p>公司: {{tenantDetail.company}}</p>
            <p>号码: {{tenantDetail.phone}}</p>
            <p>邮箱: {{tenantDetail.email}}</p>
            <p>角色: {{tenantDetail.roleNames}}</p>
            <p>权限: {{tenantDetail.permissionNames}}</p>
            <p>创建时间: {{tenantDetail.createDate}}</p>
            <p>更新时间: {{tenantDetail.updateDate}}</p>
          </div>
        </div>
      </div>
    </ModalDialog>

    <!--
   点击---租户修改
   -->
    <ModalDialog title="租户修改" :close-click="tenantUpdateModalCloseClick"
                 :sure-click="tenantUpdateModalSureClick" v-show="tenantUpdateModalDialogShow">
      <div slot="modalBody">
        <div class="form-group top15">
          <div class="row">
            <label class="col-sm-2 text-center">账号</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="tenant name (长度6~16)"
                     v-model="tenantUpdateInfo.tenantName">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">密码</label>
            <div class="col-sm-9">
              <input type="password" class="form-control" placeholder="tenant password (长度8~24)"
                     v-model="tenantUpdateInfo.password">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">公司</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="tenant company (长度2~32)"
                     v-model="tenantUpdateInfo.company">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">邮箱</label>
            <div class="col-sm-9">
              <input type="email" class="form-control" placeholder="tenant email" v-model="tenantUpdateInfo.email">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">电话</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="tenant phone" v-model="tenantUpdateInfo.phone">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">头像</label>
            <div class="col-sm-9">
              <input type="file" class="file-upload" @change="selectUpdateLogo">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">角色</label>
            <div class="col-sm-9">
              <label class="checkbox-inline" v-for="(item, index) in adminRoleInfos" :key="index">
                <input type="checkbox" :disabled="item.id<3" :value="item.id"
                       v-model="checkTenantRoleIds"> {{item.roleName}}
              </label>
            </div>
          </div>
        </div>
      </div>
    </ModalDialog>

  </div>
</template>

<script>

  import ModalDialog from './comment/ModelDialog'

  export default {
    name: 'TortoiseTenant',
    components: {
      ModalDialog
    },
    data () {
      return {
        tenantInfos: [],
        isAdmin: false,
        tenantAddModalDialogShow: false,
        tenantDetailModalDialogShow: false,
        tenantUpdateModalDialogShow: false,
        //全局监控：管理员对租户做了操作
        tenantOperate: false,
        //添加的租户信息
        tenantInfo: {
          tenantName: '',
          password: '',
          company: '',
          email: null,
          phone: null,
          headFile: null,
          statusValue: '开放',
          status: 1
        },
        //验证条件
        validatePass: {
          tenantName: false,
          password: false,
          company: false,
          headFile: false
        },
        //租户详情
        tenantDetail: {},
        //租户修改信息
        tenantUpdateInfo: {},
        //管理员注册的角色权限
        adminRoleInfos: [],
        //当前租户的角色名称
        currentTenantRoleNames: '',
        //选择租户角色名称的数据
        checkTenantRoleIds: [2],
        //当前点击item的index
        currentTenantIndex: 0
      }
    },
    mounted () {
      let authInfo = this.$global.getAuthInfo()
      if (authInfo.object.roleIds === '1') {
        this.isAdmin = true
        if (this.$global.getTenantInfos() == null) {
          this.$axios.get(this.$global.url.tenant.get)
            .then(response => {
              if (response !== null) {
                let tenantInfoArr = response.data.data.filter(value => value.roleIds !== '1')
                this.tenantInfos = tenantInfoArr
                //缓存租户信息
                sessionStorage.setItem(this.$global.cacheKey.tenantInfosKey, JSON.stringify(tenantInfoArr))
              }
            })
        } else {
          this.tenantInfos = this.$global.getTenantInfos()
        }
      } else {
        this.tenantInfos.push(this.$global.getAuthInfo().object)
      }
    },
    beforeDestroy () {
      if (this.tenantOperate === true) {
        sessionStorage.setItem(this.$global.cacheKey.tenantInfosKey, JSON.stringify(this.tenantInfos))
        this.tenantOperate = false
      }
    },
    methods: {
      //选择logo文件
      selectLogo (event) {
        this.tenantInfo.headFile = event.target.files[0]
        this.validatePass.headFile = /\.(png|jpg|gif)$/i.test(this.tenantInfo.headFile.name) && this.tenantInfo.headFile.size < 1024 * 2048
      },
      selectUpdateLogo (event) {
        this.tenantUpdateInfo.headFile = event.target.files[0]
        this.validatePass.headFile = /\.(png|jpg|gif)$/i.test(this.tenantUpdateInfo.headFile.name) && this.tenantUpdateInfo.headFile.size < 1024 * 2048
      },
      //收集管理员所有角色
      getAdminRoleInfos () {
        if (this.adminRoleInfos.length === 0) {
          if (this.$global.getRoleInfos() == null) {
            this.$axios.get(this.$global.url.role.get)
              .then(response => {
                if (response !== null) {
                  this.adminRoleInfos = response.data.data
                  //缓存角色信息
                  sessionStorage.setItem(this.$global.cacheKey.roleInfosKey, JSON.stringify(response.data.data))
                }
              })
          } else {
            this.adminRoleInfos = this.$global.getRoleInfos()
          }
        }
      },
      /**
       * 租户添加
       */
      addTenantModalCloseClick () {
        this.tenantAddModalDialogShow = false
      },
      addTenantModalSureClick () {
        this.tenantInfo.status = this.tenantInfo.statusValue === '开放' ? 1 : 0
        if (this.validatePass.tenantName && this.validatePass.password && this.validatePass.company) {
          this.tenantAddModalDialogShow = false
          let d = new Date()
          let currentDate = d.getFullYear() + '-' + d.getMonth() + '-' + d.getDate() + ' ' + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds()
          let roleIds = ''
          this.checkTenantRoleIds.forEach(value => roleIds += value + ',')
          this.tenantInfo.roleIds = roleIds
          let formData = new FormData()
          formData.append('tenantName', this.tenantInfo.tenantName)
          formData.append('password', this.tenantInfo.password)
          formData.append('company', this.tenantInfo.company)
          if (this.tenantInfo.email != null && this.tenantInfo.email.length > 0) {
            formData.append('email', this.tenantInfo.email)
          }
          if (this.tenantInfo.phone != null && this.tenantInfo.phone.length > 0) {
            formData.append('phone', this.tenantInfo.phone)
          }
          if (this.tenantInfo.roleIds != null && this.tenantInfo.roleIds.length > 0) {
            formData.append('roleIds', this.tenantInfo.roleIds)
          }
          if (this.validatePass.headFile) {
            formData.append('headFile', this.tenantInfo.headFile)
          }
          formData.append('status', this.tenantInfo.status)
          this.$axios.post(this.$global.url.tenant.add, formData)
            .then(response => {
              if (response !== null) {
                let tenantInfo = response.data.data
                tenantInfo.updateDate = currentDate
                tenantInfo.createDate = currentDate
                this.tenantInfos.push(tenantInfo)
                //记录租户操作
                this.tenantOperate = true
                //提示
                this.$pubSub.publish('toastShow', {
                  type: 'success',
                  msg: '租户信息添加成功'
                })
              }
            })
        } else {
          this.$pubSub.publish('toastShow', {
            type: 'warning',
            msg: '请按要求填写必要信息,谢谢!!'
          })
        }
      },
      addTenantClick () {
        this.tenantInfo = {
          tenantName: '',
          password: '',
          company: '',
          email: null,
          phone: null,
          statusValue: '开放',
          status: 1
        }
        this.checkTenantRoleIds = [2]
        this.getAdminRoleInfos()
        this.tenantAddModalDialogShow = true
      },
      /**
       * 租户详情
       */
      tenantDetailModalCloseClick () {
        this.tenantDetailModalDialogShow = false
      },
      tenantDetailModalSureClick () {
        this.tenantDetailModalDialogShow = false
      },
      /**
       * 租户修改
       */
      tenantUpdateModalCloseClick () {
        this.tenantUpdateModalDialogShow = false
      },
      tenantUpdateModalSureClick () {
        let d = new Date()
        let currentDate = d.getFullYear() + '-' + d.getMonth() + '-' + d.getDate() + ' ' + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds()
        let roleIds = ''
        this.checkTenantRoleIds.forEach(value => roleIds += value + ',')
        this.tenantUpdateInfo.status = this.tenantUpdateInfo.statusValue === '开放' ? 1 : 0
        this.tenantUpdateInfo.roleIds = roleIds
        this.tenantUpdateModalDialogShow = false
        let formData = new FormData()
        formData.append('tenantId', this.tenantUpdateInfo.tenantId)
        formData.append('tenantName', this.tenantUpdateInfo.tenantName)
        if (this.tenantUpdateInfo.password != null && this.tenantUpdateInfo.password.length > 7) {
          formData.append('password', this.tenantUpdateInfo.password)
        }
        formData.append('company', this.tenantUpdateInfo.company)
        if (this.tenantUpdateInfo.email != null && this.tenantUpdateInfo.email.length > 0) {
          formData.append('email', this.tenantUpdateInfo.email)
        }
        if (this.tenantUpdateInfo.phone != null && this.tenantUpdateInfo.phone.length > 0) {
          formData.append('phone', this.tenantUpdateInfo.phone)
        }
        if (this.tenantUpdateInfo.roleIds != null && this.tenantUpdateInfo.roleIds.length > 0) {
          formData.append('roleIds', this.tenantUpdateInfo.roleIds)
        }
        if (this.validatePass.headFile) {
          formData.append('headFile', this.tenantUpdateInfo.headFile)
        }
        this.$axios.put(this.$global.url.tenant.update, formData)
          .then(response => {
            if (response !== null) {
              let tenantInfo = response.data.data
              tenantInfo.updateDate = currentDate
              //更新当前显示租户信息
              this.tenantInfos.splice(this.currentTenantIndex, 1, tenantInfo)
              //记录租户操作
              this.tenantOperate = true
              //提示
              this.$pubSub.publish('toastShow', {
                type: 'success',
                msg: '修改租户信息成功'
              })
            }
          })
      },
      /**
       * 租户每个item的点击
       */
      delegateAppItemClick (e, item, index) {
        this.currentTenantIndex = index
        switch (e.target.innerText) {
          case '禁用':
          case '开放':
            let tenantStatusInfo = {}
            tenantStatusInfo.status = item.status === 1 ? 0 : 1
            tenantStatusInfo.tenantId = item.tenantId
            this.$axios.put(this.$global.url.tenant.updateTenantStatus, tenantStatusInfo)
              .then(response => {
                if (response !== null) {
                  if (item.status === 1) {
                    item.status = 0
                    item.statusValue = '禁用'
                  } else {
                    item.status = 1
                    item.statusValue = '开放'
                  }
                }
                //记录租户操作
                this.tenantOperate = true
                //提示
                this.$pubSub.publish('toastShow', {
                  type: 'success',
                  msg: '租户状态修改成功'
                })
              })
            break
          case '详情':
            if (item.roleInfos) {
              let roleNames = ''
              let permissionNames = ''
              item.roleInfos.forEach(roleInfo => {
                roleNames += roleInfo.roleName + ' '
                if (roleInfo.permissionInfos !== null) {
                  roleInfo.permissionInfos.forEach(permission => {
                    permissionNames += permission.permissionName + ' '
                  })
                }
              })
              item.roleNames = roleNames
              item.permissionNames = permissionNames
            }
            this.tenantDetail = item
            this.tenantDetailModalDialogShow = true
            break
          case '修改':
            //给选择的租户id赋值
            this.checkTenantRoleIds = []
            if (item.roleInfos) {
              item.roleInfos.forEach(roleInfo => this.checkTenantRoleIds.push(roleInfo.id))
            }
            this.tenantUpdateInfo = item
            //获取所有admin定义的角色
            this.getAdminRoleInfos()
            this.tenantUpdateModalDialogShow = true
            break
          default:
            break
        }
      }
    },
    watch: { //监视
      'tenantInfo.tenantName': {
        deep: true,
        handler: function (newValue, oldValue) {
          this.validatePass.tenantName = newValue.length > 5 && newValue.length < 15
        }
      },
      'tenantInfo.password': {
        deep: true,
        handler: function (newValue, oldValue) {
          this.validatePass.password = newValue.length > 7 && newValue.length < 19
        }
      },
      'tenantInfo.company': {
        deep: true,
        handler: function (newValue, oldValue) {
          this.validatePass.company = newValue.length > 0 && newValue.length < 32
        }
      }
    }
  }
</script>

<style scoped>

</style>



