<template>
  <div class="topNav">
    <button type="button" class="btn btn-sm btn-success" @click="addRoleClick">新增角色</button>
    <div class="row">
      <div class="col-sm-3 top15" v-for="(item, index) in roleInfos" :key="index"
           @click="delegateAppItemClick($event,item,index)">
        <div class="panel panel-default">
          <div class="panel-heading">
            <h3 class="panel-title">{{item.roleName}}</h3>
          </div>
          <div class="panel-body">
            <ul class="list-unstyled">
              <li class="li_style_bottom li_style_height" v-for="(item1, index1) in item.permissionInfos" :key="index1">
                {{item1.permissionName}}
              </li>
            </ul>
          </div>
          <div class="panel-footer text-center">
            <button type="button" class="btn btn-xs btn-danger" v-show="item.id>2">删除</button>
            <button type="button" class="btn btn-xs btn-info">详情</button>
            <button type="button" class="btn btn-xs btn-default" v-show="item.id>2">修改</button>
          </div>
        </div>
      </div>
    </div>

    <!--
点击---角色添加
-->
    <ModalDialog title="角色添加" :close-click="addRoleModalCloseClick"
                 :sure-click="addRoleModalSureClick" v-show="roleAddModalDialogShow">
      <div slot="modalBody">
        <div class="form-group top15">
          <div class="row">
            <label class="col-sm-2 text-center">角色名称</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="role name (长度1~16)"
                     v-model="roleInfo.roleName">
            </div>
            <span class="text-info small">必填</span>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">角色标记</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="role tag (长度1~32)"
                     v-model="roleInfo.roleTag">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">角色描述</label>
            <div class="col-sm-9">
              <textarea rows="3" type="text" class="form-control" placeholder="role description (长度1~128)"
                        v-model="roleInfo.roleDesc"></textarea>
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">角色类型</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="role type (长度2~32)" v-model="roleInfo.roleType" :disabled="!validatePass.roleName">
            </div>
            <span class="text-info small">必填</span>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">权限</label>
            <div class="col-sm-9">

              <table class="table table-striped table-bordered table-hover text-center">
                <thead>
                <tr>
                  <th class="text-center">类型</th>
                  <th class="text-center">操作</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(item, index) in transformPermissions" :key="index">
                  <td>{{item.permissionType}}</td>
                  <td>
                    <label class="checkbox-inline" v-for="(item1, index1) in item.permissions" :key="index1">
                      <input type="checkbox" :value="item1.id" :disabled="item1.id<3"
                             v-model="checkPermissionIds"> {{item1.permissionName}}
                    </label>
                  </td>
                </tr>
                </tbody>
              </table>

            </div>
            <span class="text-info small">必选</span>
          </div>
        </div>
      </div>
    </ModalDialog>

    <!--
点击---角色详情
-->
    <ModalDialog title="角色详情" :close-click="roleDetailModalCloseClick"
                 :sure-click="roleDetailModalSureClick" v-show="roleDetailModalDialogShow">
      <div slot="modalBody">
        <div class="row">
          <div class="col-sm-1"></div>
          <div class="col-sm-11">
            <h3 class="h3">{{roleDetail.roleName}}</h3>
            <p>标记: {{roleDetail.roleTag}}</p>
            <p>类型: {{roleDetail.roleType}}</p>
            <p>描述: {{roleDetail.roleDesc}}</p>
            <p>权限: {{roleDetail.permissionNames}}</p>
            <p>创建时间: {{roleDetail.createDate}}</p>
            <p>更新时间: {{roleDetail.updateDate}}</p>
          </div>
        </div>
      </div>
    </ModalDialog>

    <!--
点击---角色修改
-->
    <ModalDialog title="角色修改" :close-click="updateRoleModalCloseClick"
                 :sure-click="updateRoleModalSureClick" v-show="roleUpdateModalDialogShow">
      <div slot="modalBody">
        <div class="form-group top15">
          <div class="row">
            <label class="col-sm-2 text-center">角色名称</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="role name (长度1~16)"
                     v-model="roleUpdateInfo.roleName">
            </div>
          </div>
        </div>
        <div class="form-group" v-show="isAdmin">
          <div class="row">
            <label class="col-sm-2 text-center">角色标记</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="role tag (长度1~32)"
                     v-model="roleUpdateInfo.roleTag" disabled>
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">角色描述</label>
            <div class="col-sm-9">
              <textarea rows="3" type="text" class="form-control" placeholder="role description (长度1~128)"
                        v-model="roleUpdateInfo.roleDesc"></textarea>
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">角色类型</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="role type (长度2~32)"
                     v-model="roleUpdateInfo.roleType">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">权限</label>
            <div class="col-sm-9">
              <table class="table table-striped table-bordered table-hover text-center">
                <thead>
                <tr>
                  <th class="text-center">类型</th>
                  <th class="text-center">操作</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(item, index) in transformPermissions" :key="index">
                  <td>{{item.permissionType}}</td>
                  <td>
                    <label class="checkbox-inline" v-for="(item1, index1) in item.permissions" :key="index1">
                      <input type="checkbox" :value="item1.id" :disabled="item1.id<3"
                             v-model="checkPermissionIds"> {{item1.permissionName}}
                    </label>
                  </td>
                </tr>
                </tbody>
              </table>

            </div>
          </div>
        </div>
      </div>
    </ModalDialog>


  </div>

</template>

<script>

  import ModalDialog from './comment/ModelDialog'
  //引入vue-cli自带的uuid库
  // import uuidv1 from 'uuid/v1'

  export default {
    name: 'TortoiseRole',
    components: {
      ModalDialog
    },
    data () {
      return {
        //认证信息
        authInfo: {},
        //展示 角色信息
        roleInfos: [],
        //全局显示flag
        isAdmin: false,
        roleAddModalDialogShow: false,
        roleDetailModalDialogShow: false,
        roleUpdateModalDialogShow: false,
        //全局监控：管理员对租户做了操作
        roleOperate: false,
        roleInfo: {
          roleName: '',
          roleTag: '',
          roleDesc: '',
          roleType: ''
        },
        validatePass: {
          roleName: false,
          roleType: false
        },
        //租户权限
        tenantPermissionInfos: [],
        //转换 分类 权限信息
        transformPermissionMap: new Map(),
        //转换 展示 权限信息
        transformPermissions: [],
        //选择的权限
        checkPermissionIds: [],
        //角色详情
        roleDetail: {},
        //修改的角色信息
        roleUpdateInfo: {},
        //当前点击item的index
        currentRoleIndex: 0
      }
    },
    mounted () {
      let authInfo = this.$global.getAuthInfo()
      this.authInfo = authInfo
      if (authInfo.object.roleIds === '1') {
        this.isAdmin = true
        //获取租户的权限
        if (this.$global.getPermissionInfos() == null) {
          this.$axios.get(this.$global.url.permission.get)
            .then(response => {
              if (response !== null) {
                //缓存权限信息
                this.cachePermissionInfos = response.data.data
                sessionStorage.setItem(this.$global.cacheKey.permissionInfosKey, JSON.stringify(response.data.data))
                //转化为展示数据
                this.transformPermissionInfos(response.data.data)
              }
            })
        } else {
          this.cachePermissionInfos = this.$global.getPermissionInfos()
          this.transformPermissionInfos(this.cachePermissionInfos)
        }
      }else {
        let permissionInfos = []
        let roleInfos = this.$global.getAuthInfo().object.roleInfos
        roleInfos.forEach(roleInfo => {
          if (roleInfo.permissionInfos !== null) {
            roleInfo.permissionInfos.forEach(value => permissionInfos.push(value))
          }
        })
        this.transformPermissionInfos(permissionInfos)
      }
      //如何展示租户的角色
      if (this.$global.getRoleInfos() == null) {
        if (authInfo.object.roleIds === '1') {
          //管理员
          this.$axios.get(this.$global.url.role.get)
            .then(response => {
              if (response !== null) {
                this.roleInfos = response.data.data
                //缓存角色信息
                sessionStorage.setItem(this.$global.cacheKey.roleInfosKey, JSON.stringify(response.data.data))
              }
            })
        } else {
          //租户
          //获取租户创建的所有角色（不包括管理员分配给租户的角色）
          this.$axios.get(this.$global.url.role.get)
            .then(response => {
              if (response !== null) {
                let roles = response.data.data
                if (roles != null && roles.length > 0) {
                  this.roleInfos = roles
                  //缓存角色信息
                  sessionStorage.setItem(this.$global.cacheKey.roleInfosKey, JSON.stringify(this.roleInfos))
                } else {
                  //缓存角色信息
                  sessionStorage.setItem(this.$global.cacheKey.roleInfosKey, JSON.stringify(this.roleInfos))
                }
              }
            })
        }
      } else {
        this.roleInfos = this.$global.getRoleInfos()
      }
    },
    beforeDestroy () {
      if (this.roleOperate === true) {
        sessionStorage.setItem(this.$global.cacheKey.roleInfosKey, JSON.stringify(this.roleInfos))
        this.roleOperate = false
      }
    },
    methods: {
      /**
       * 权限信息转换展示的权限信息
       */
      transformPermissionInfos (permissions) {
        this.transformPermissionMap.clear()
        permissions.forEach(value => {
          let valueArr = this.transformPermissionMap.get(value.permissionType)
          if (valueArr != null) {
            valueArr.push(value)
          } else {
            valueArr = []
            valueArr.push(value)
          }
          this.transformPermissionMap.set(value.permissionType, valueArr)
        })
        this.transformPermissionMap.forEach((value, key) => {
          let permissionObject = {}
          permissionObject.permissionType = key
          permissionObject.permissions = value
          this.transformPermissions.push(permissionObject)
        })
      },
      /**
       * 角色添加
       */
      addRoleClick () {
        this.roleAddModalDialogShow = true
      },
      addRoleModalCloseClick () {
        this.roleAddModalDialogShow = false
      },
      addRoleModalSureClick () {
        if (this.validatePass.roleName && this.validatePass.roleType) {
          this.roleAddModalDialogShow = false
          let d = new Date()
          let currentDate = d.getFullYear() + '-' + d.getMonth() + '-' + d.getDate() + ' ' + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds()
          let permissionIds = ''
          this.checkPermissionIds.forEach(value => permissionIds += value + ',')
          this.roleInfo.permissionIds = permissionIds
          this.$axios.post(this.$global.url.role.add, this.roleInfo)
            .then(response => {
              if (response !== null) {
                let roleInfo = response.data.data
                roleInfo.updateDate = currentDate
                roleInfo.createDate = currentDate
                this.roleInfos.push(roleInfo)
                //记录操作
                this.roleOperate = true
                //提示
                this.$pubSub.publish('toastShow', {
                  type: 'success',
                  msg: '添加角色信息成功'
                })
              }
            })
        } else {
          this.$pubSub.publish('toastShow', {
            type: 'warning',
            msg: '请按要求填写必要信息,谢谢!!'
          })
        }
      },
      /**
       * 角色详情
       */
      roleDetailModalCloseClick () {
        this.roleDetailModalDialogShow = false
      },
      roleDetailModalSureClick () {
        this.roleDetailModalDialogShow = false
      },
      /**
       * 角色修改
       */
      updateRoleModalCloseClick () {
        this.roleUpdateModalDialogShow = false
      },
      updateRoleModalSureClick () {
        let d = new Date()
        let currentDate = d.getFullYear() + '-' + d.getMonth() + '-' + d.getDate() + ' ' + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds()
        let permissionIds = ''
        this.checkPermissionIds.forEach(value => permissionIds += value + ',')
        this.roleUpdateInfo.permissionIds = permissionIds
        this.roleUpdateModalDialogShow = false
        this.$axios.put(this.$global.url.role.update, this.roleUpdateInfo)
          .then(response => {
            if (response !== null) {
              let roleInfo = response.data.data
              roleInfo.updateDate = currentDate
              //更新当前显示角色信息
              this.roleInfos.splice(this.currentRoleIndex, 1, roleInfo)
              //记录操作
              this.roleOperate = true
              //提示
              this.$pubSub.publish('toastShow', {
                type: 'success',
                msg: '修改角色信息成功'
              })
            }
          })

      },
      /**
       * 角色每个item的点击
       */
      delegateAppItemClick (e, item, index) {
        switch (e.target.innerText) {
          case '删除':
            this.$axios.delete(this.$global.url.role.delete, {
              params: {
                id: item.id
              }
            })
              .then(response => {
                if (response !== null) {
                  this.roleInfos.splice(index, 1)
                  //记录操作
                  this.roleOperate = true
                  //提示
                  this.$pubSub.publish('toastShow', {
                    type: 'success',
                    msg: '角色删除成功'
                  })
                }
              })
            break
          case '详情':
            let permissionNames = ''
            if (item.permissionInfos != null && item.permissionInfos.length > 0) {
              item.permissionInfos.forEach(permission => {
                permissionNames += permission.permissionName + ' '
              })
            }
            item.permissionNames = permissionNames
            this.roleDetail = item
            this.roleDetailModalDialogShow = true
            break
          case '修改':
            //给选择的租户id赋值
            this.checkPermissionIds = []
            if (item.permissionIds != null && item.permissionIds.length > 0) {
              let permissionIdsSplit = item.permissionIds.split(',')
              permissionIdsSplit.forEach(permissionId => this.checkPermissionIds.push(permissionId))
            }
            this.roleUpdateInfo = item
            this.roleUpdateModalDialogShow = true
            this.currentRoleIndex = index
            break
          default:
            break
        }
      }
    },
    watch: { //监视
      'roleInfo.roleName': {
        deep: true,
        handler: function (newValue, oldValue) {
          this.validatePass.roleName = newValue.length > 0 && newValue.length < 15
        }
      },
      'roleInfo.roleType': {
        deep: true,
        handler: function (newValue, oldValue) {
          this.validatePass.roleType = newValue.length > 0 && newValue.length < 32
        }
      }
    }
  }
</script>

<style scoped>
  .li_style_bottom:not(:last-child) {
    border-bottom: #d6d6d6 1px solid;
  }

  .li_style_height {
    min-height: 32px;
    line-height: 32px;
  }
</style>



