<template>
  <div class="login_bg">
    <div class="col-sm-4 vertical_center">
      <h1 class="text-center" style="color: white;margin-bottom: 15px">Tortoise tenant system</h1>
      <div class="panel panel-default out_shadow">
        <div class="panel-body">
          <div class="form-group">
            <label>用户名</label><span class="small text-info pull-right">(长度6~16)</span>
            <input type="text" class="form-control" placeholder="请输入用户名" v-model="userInfo.username">
          </div>
          <div class="form-group">
            <label>密码</label><span class="small text-info pull-right">(长度8~20)</span>
            <input type="password" class="form-control" placeholder="请输入密码" :disabled="!validatePass.username"
                   v-model="userInfo.password">
          </div>
          <div class="text-center">
            <button class="btn btn-default" type="button" @click="resetClick">重置</button>
            <button class="btn btn-primary"  type="button"
                    @click="loginClick">登录
            </button>
          </div>
        </div>
      </div>
    </div>

    <!--    提示框-->
    <Toast :type="toast.type" :msg="toast.msg" v-show="toastShow"></Toast>
  </div>
</template>

<script>

  import Toast from './comment/Toast'

  export default {
    name: 'TortoiseLogin',
    components: {
      Toast
    },
    created(){
      this.$global.destroy();
    },
    mounted () {
      this.$global.destroy();
    },
    data () {
      return {
        userInfo: {
          username: '',
          password: ''
        },
        validatePass: {
          username: false,
          password: false
        },
        //用户提示显示
        toastShow: false,
        toast: {
          type: 'warning',
          msg: ''
        }
      }
    },
    methods: {
      resetClick () {
        this.userInfo = {
          username: '',
          password: ''
        }
      },
      loginClick () {
        this.$axios.post(this.$global.url.tenant.auth, this.userInfo)
          .then(response => {
            if (response !== null) {
              //登录后请求头设置token
              this.$axios.defaults.headers.common['token'] = response.data.data.tokenValue
              //缓存用户信息
              sessionStorage.setItem(this.$global.cacheKey.authInfoKey, JSON.stringify(response.data.data))
              //跳转
              this.$router.push('/')
            }
          })
      }
    },
    watch: { //监视
      'userInfo.username': {
        deep: true,
        handler: function (newValue, oldValue) {
          this.validatePass.username = newValue.length > 5 && newValue.length < 15
        }
      },
      'userInfo.password': {
        deep: true,
        handler: function (newValue, oldValue) {
          this.validatePass.password = newValue.length > 7 && newValue.length < 19
        }
      }
    }
  }
</script>

<style scoped>

  .login_bg {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    /*background-image: linear-gradient(to right , #4a4c57, #cdcecb);*/
    background-image: radial-gradient(circle, #c8bfca, #545451);
  }

  .out_shadow {
    box-shadow: -5px 0px 5px #b1b1b8, 0px -5px 5px #b1b1b8, 5px 0px 5px #b1b1b8, 0px 5px 5px #b1b1b8;
  }

  .vertical_center {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%)
  }
</style>



