// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import PubSub from 'pubsub-js'
import global from './global.vue'

/**
 * axios全局设置
 */
// 超时时间10秒
axios.defaults.timeout = 10000
// 基本地址
axios.defaults.baseURL = global.url.base
//设置token
axios.defaults.headers.common['token'] = null
//请求之前
axios.interceptors.request.use(
  config => {
    PubSub.publish('progressShow', true)
    return config
  },
  err => {
    return Promise.reject(err)
  }
)
// 请求之后
axios.interceptors.response.use(
  response => {
    PubSub.publish('progressShow', false)
    if (response.data.status === 240) {
      PubSub.publish('toastShow', {
        type: 'error',
        msg: response.data.errorInfo
      })
      //token过期处理
      if (response.data.errorInfo === 'token invalid' || response.data.errorInfo === '认证失效') {
        let timer = setTimeout(() => {
          router.replace('/login')
          clearTimeout(timer)
        }, 2000)
      }
      return null
    } else {
      return response
    }
  },
  error => {
    PubSub.publish('progressShow', false)
    PubSub.publish('toastShow', {
      type: 'error',
      msg: '服务器异常,请稍后重试'
    })
    return Promise.reject(error.response)
  })
/**
 * 全局设置公共库
 */
Vue.prototype.$axios = axios
Vue.prototype.$pubSub = PubSub
Vue.prototype.$global = global

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {App},
  template: '<App/>'
})



