<template>
  <div id="app" >
    <router-view></router-view>
    <!--   进度条-->
    <Progress v-show="progressShow"></Progress>
    <!--    提示框-->
    <Toast :type="toast.type" :msg="toast.msg" v-show="toastShow"></Toast>
  </div>
</template>

<script>

  import Progress from './components/comment/Progress'
  import Toast from './components/comment/Toast'

  export default {
    name: 'App',
    components: {
      Progress,
      Toast
    },
    data () {
      return {
        //加载进度条显示
        progressShow: false,
        //用户提示显示
        toastShow: false,
        toast: {
          type: 'info',
          msg: ''
        }
      }
    },
    created () {
      let authInfo = this.$global.getAuthInfo()
      if (authInfo == null) {
        this.$router.replace('/login')
      }else {
        this.$axios.defaults.headers.common['token'] = authInfo.tokenValue
      }
    },
    mounted () {
      this.$pubSub.subscribe('progressShow', (msg, data) => {
        this.progressShow = data
      })
      this.$pubSub.subscribe('toastShow', (msg, data) => {
        this.toastShow = true
        this.toast = data
        let timer = setTimeout(() => {
          this.toastShow = false
          clearTimeout(timer)
        }, 3000)
      })
    }
  }
</script>

<style>
</style>



