import Vue from 'vue'
import Router from 'vue-router'

import TortoiseLogin from '../components/TortoiseLogin'
import TortoiseIndex from '../components/TortoiseIndex'
import TortoiseTenant from '../components/TortoiseTenant'
import TortoiseUser from '../components/TortoiseUser'
import TortoiseDepartment from '../components/TortoiseDepartment'
import TortoiseRole from '../components/TortoiseRole'
import TortoisePermission from '../components/TortoisePermission'
import TortoisePersonal from '../components/TortoisePersonal'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: 'index'
    },
    {
      path: '/index',
      component: TortoiseIndex,
      children:[
        {
          path: 'tenant',
          component: TortoiseTenant,
        },
        {
          path: 'user',
          component: TortoiseUser
        },
        {
          path: 'role',
          component: TortoiseRole
        },
        {
          path: 'permission',
          component: TortoisePermission
        },
        {
          path: 'department',
          component: TortoiseDepartment
        },
        {
          path: 'personal',
          component: TortoisePersonal
        }
      ]
    },
    {
      path: '/login',
      component: TortoiseLogin
    }
  ]
})



