<template>
  <div class="topNav">

    <button type="button" class="btn btn-sm btn-success" v-show="isAdmin" @click="addPermissionClick">新增权限</button>

    <div class="row">
      <div class="col-sm-4 top15" v-for="(item, index) in permissionInfos" :key="index">
        <div class="panel panel-default">
          <div class="panel-heading">
            <h3 class="panel-title">{{item.permissionType}}
              <button type="button" class="btn btn-xs btn-success pull-right" v-show="isAdmin"
                      @click="delegateItemAddClick($event,item,index)">添加
              </button>
            </h3>
          </div>
          <div class="panel-body">
            <ul class="list-unstyled">
              <li class="li_style_bottom li_style_height"
                  v-for="(item1, index1) in item.permissions" :key="index1"
                  @click="delegateAppItemClick($event,item1,index1)">
                {{item1.permissionName}}
                <div class="pull-right">
                  <button type="button" class="btn btn-xs btn-danger" v-show="isAdmin && item1.id>2">删除</button>
                  <button type="button" class="btn btn-xs btn-info">详情</button>
                  <button type="button" class="btn btn-xs btn-default" v-show="isAdmin && item1.id>2">修改</button>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>

    <!--
点击---权限添加
-->
    <ModalDialog title="权限添加" :close-click="addPermissionModalCloseClick"
                 :sure-click="addPermissionModalSureClick" v-show="permissionAddModalDialogShow">
      <div slot="modalBody">
        <div class="form-group top15">
          <div class="row">
            <label class="col-sm-2 text-center">权限名称</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="permission name (长度1~16)"
                     v-model="permissionInfo.permissionName">
            </div>
            <span class="text-info small">必填</span>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">权限标记</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="permission tag (长度1~32)"
                     v-model="permissionInfo.permissionTag" :disabled="!validatePass.permissionName">
            </div>
            <span class="text-info small">必填</span>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">权限描述</label>
            <div class="col-sm-9">
              <textarea rows="3" type="text" class="form-control" placeholder="permission description (长度1~128)"
                        v-model="permissionInfo.permissionDesc"></textarea>
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">权限类型</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="permission type (长度2~32)"
                     v-model="permissionInfo.permissionType" :disabled="!validatePass.permissionTag">
            </div>
            <span class="text-info small">必填</span>
          </div>
        </div>
      </div>
    </ModalDialog>

    <!--
点击---权限详情
-->
    <ModalDialog title="权限详情" :close-click="permissionDetailModalCloseClick"
                 :sure-click="permissionDetailModalSureClick" v-show="permissionDetailModalDialogShow">
      <div slot="modalBody">
        <div class="row">
          <div class="col-sm-1"></div>
          <div class="col-sm-11">
            <h3 class="h3">{{permissionDetail.permissionName}}</h3>
            <p>标记: {{permissionDetail.permissionTag}}</p>
            <p>类型: {{permissionDetail.permissionType}}</p>
            <p>描述: {{permissionDetail.permissionDesc}}</p>
            <p>创建时间: {{permissionDetail.createDate}}</p>
            <p>更新时间: {{permissionDetail.updateDate}}</p>
          </div>
        </div>
      </div>
    </ModalDialog>

    <!--
点击---权限修改
-->
    <ModalDialog title="权限修改" :close-click="updatePermissionModalCloseClick"
                 :sure-click="updatePermissionModalSureClick" v-show="permissionUpdateModalDialogShow">
      <div slot="modalBody">
        <div class="form-group top15">
          <div class="row">
            <label class="col-sm-2 text-center">权限名称</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="permission name (长度1~16)"
                     v-model="permissionUpdateInfo.permissionName">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">权限标记</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="permission tag (长度1~32)"
                     v-model="permissionUpdateInfo.permissionTag" disabled>
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">权限描述</label>
            <div class="col-sm-9">
              <textarea rows="3" type="text" class="form-control" placeholder="permission description (长度1~128)"
                        v-model="permissionUpdateInfo.permissionDesc"></textarea>
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">权限类型</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="permission type (长度2~32)"
                     v-model="permissionUpdateInfo.permissionType" disabled>
            </div>
          </div>
        </div>
      </div>
    </ModalDialog>
  </div>
</template>

<script>

  import ModalDialog from './comment/ModelDialog'

  export default {
    name: 'TortoisePermission',
    components: {
      ModalDialog
    },
    data () {
      return {
        //展示 权限信息
        permissionInfos: [],
        //分类 权限信息
        transformPermissionMap: new Map(),
        //缓存 权限信息
        cachePermissionInfos: [],
        //全局显示flag
        isAdmin: false,
        permissionAddModalDialogShow: false,
        permissionDetailModalDialogShow: false,
        permissionUpdateModalDialogShow: false,
        //全局监控：管理员对权限做了操作
        permissionOperate: false,
        permissionInfo: {
          permissionName: '',
          permissionTag: '',
          permissionDesc: '',
          permissionType: ''
        },
        validatePass: {
          permissionName: false,
          permissionTag: false,
          permissionType: false
        },
        //权限详情
        permissionDetail: {},
        //修改权限信息
        permissionUpdateInfo: {},
        //当前权限索引
        currentPermissionIndex: 0
      }
    },
    mounted () {
      let authInfo = this.$global.getAuthInfo()
      if (authInfo.object.roleIds === '1') {
        this.isAdmin = true
        if (this.$global.getPermissionInfos() == null) {
          this.$axios.get(this.$global.url.permission.get)
            .then(response => {
              if (response !== null) {
                //缓存权限信息
                this.cachePermissionInfos = response.data.data
                sessionStorage.setItem(this.$global.cacheKey.permissionInfosKey, JSON.stringify(response.data.data))
                //转化为展示数据
                this.transformPermissionInfos(response.data.data)
              }
            })
        } else {
          this.cachePermissionInfos = this.$global.getPermissionInfos()
          this.transformPermissionInfos(this.cachePermissionInfos)
        }
      } else {
        let permissionInfos = []
        let roleInfos = this.$global.getAuthInfo().object.roleInfos
        roleInfos.forEach(roleInfo => {
          if (roleInfo.permissionInfos !== null) {
            roleInfo.permissionInfos.forEach(value => permissionInfos.push(value))
          }
        })
        this.transformPermissionInfos(permissionInfos)
      }
    },
    methods: {
      /**
       * 权限信息转换展示的权限信息
       */
      transformPermissionInfos (permissions) {
        this.transformPermissionMap.clear()
        permissions.forEach(value => {
          let valueArr = this.transformPermissionMap.get(value.permissionType)
          if (valueArr != null) {
            valueArr.push(value)
          } else {
            valueArr = []
            valueArr.push(value)
          }
          this.transformPermissionMap.set(value.permissionType, valueArr)
        })
        this.transformPermissionMap.forEach((value, key) => {
          let permissionObject = {}
          permissionObject.permissionType = key
          permissionObject.permissions = value
          this.permissionInfos.push(permissionObject)
        })
      },
      /**
       * 权限添加
       */
      addPermissionClick () {
        this.permissionInfo = {
          permissionName: '',
          permissionTag: '',
          permissionDesc: '',
          permissionType: ''
        }
        this.permissionAddModalDialogShow = true
      },
      addPermissionModalCloseClick () {
        this.permissionAddModalDialogShow = false
      },
      addPermissionModalSureClick () {
        if (this.validatePass.permissionTag && this.validatePass.permissionName && this.validatePass.permissionType) {
          this.permissionAddModalDialogShow = false
          let d = new Date()
          let currentDate = d.getFullYear() + '-' + d.getMonth() + '-' + d.getDate() + ' ' + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds()
          this.$axios.post(this.$global.url.permission.add, this.permissionInfo)
            .then(response => {
              if (response !== null) {
                let permissionInfo = response.data.data
                permissionInfo.updateDate = currentDate
                permissionInfo.createDate = currentDate
                //添加操作
                let valueArr = this.transformPermissionMap.get(permissionInfo.permissionType)
                if (valueArr != null) {
                  valueArr.push(permissionInfo)
                } else {
                  valueArr = []
                  valueArr.push(permissionInfo)
                }
                this.transformPermissionMap.set(permissionInfo.permissionType, valueArr)
                this.permissionInfos = []
                this.transformPermissionMap.forEach((value, key) => {
                  let permissionObject = {}
                  permissionObject.permissionType = key
                  permissionObject.permissions = value
                  this.permissionInfos.push(permissionObject)
                })
                //更新缓存权限信息
                this.cachePermissionInfos.push(permissionInfo)
                //记录权限操作
                this.permissionOperate = true
                //提示
                this.$pubSub.publish('toastShow', {
                  type: 'success',
                  msg: '添加权限信息成功'
                })
              }
            })
        } else {
          this.$pubSub.publish('toastShow', {
            type: 'warning',
            msg: '请按要求填写必要信息,谢谢!!'
          })
        }
      },
      permissionDetailModalCloseClick () {
        this.permissionDetailModalDialogShow = false
      },
      permissionDetailModalSureClick () {
        this.permissionDetailModalDialogShow = false
      },
      updatePermissionModalCloseClick () {
        this.permissionUpdateModalDialogShow = false
      },
      updatePermissionModalSureClick () {
        this.permissionUpdateModalDialogShow = false
        let d = new Date()
        let currentDate = d.getFullYear() + '-' + d.getMonth() + '-' + d.getDate() + ' ' + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds()
        this.$axios.put(this.$global.url.permission.update, this.permissionUpdateInfo)
          .then(response => {
            if (response !== null) {
              let permissionInfo = response.data.data
              permissionInfo.updateDate = currentDate
              permissionInfo.createDate = currentDate
              //更新当前显示角色信息
              this.permissionInfos.splice(this.currentPermissionIndex, 1, permissionInfo)
              //记录权限操作
              this.permissionOperate = true
              //提示
              this.$pubSub.publish('toastShow', {
                type: 'success',
                msg: '修改权限信息成功'
              })
            }
          })

      },
      delegateAppItemClick (e, item, index) {
        switch (e.target.innerText) {
          case '删除':
            this.$axios.delete(this.$global.url.permission.delete, {
              params: {
                id: item.id
              }
            })
              .then(response => {
                if (response !== null) {
                  //删除操作
                  let valueArr = this.transformPermissionMap.get(item.permissionType)
                  if (valueArr != null) {
                    valueArr.splice(index, 1)
                    if (valueArr.length === 1) {
                      this.transformPermissionMap.delete(item.permissionType)
                    }
                  }
                  this.transformPermissionMap.set(item.permissionType, valueArr)
                  this.permissionInfos = []
                  this.transformPermissionMap.forEach((value, key) => {
                    let permissionObject = {}
                    permissionObject.permissionType = key
                    permissionObject.permissions = value
                    this.permissionInfos.push(permissionObject)
                  })
                  //更新缓存的权限信息
                  for (let i = 0; i < this.cachePermissionInfos.length; i++) {
                    let cachePermissionInfo = this.cachePermissionInfos[i]
                    if (cachePermissionInfo.permissionType === item.permissionType &&
                      cachePermissionInfo.permissionName === item.permissionName) {
                      this.cachePermissionInfos.splice(i, 1)
                      break
                    }
                  }
                  //
                  //记录权限操作
                  this.permissionOperate = true
                  //提示
                  this.$pubSub.publish('toastShow', {
                    type: 'success',
                    msg: '权限删除成功'
                  })
                }
              })
            break
          case '详情':
            this.permissionDetail = item
            this.permissionDetailModalDialogShow = true
            break
          case '修改':
            this.permissionUpdateInfo = item
            this.currentPermissionIndex = index
            this.permissionUpdateModalDialogShow = true
            break
          default:
            break
        }
      },
      delegateItemAddClick ($event, item, index) {
        this.permissionInfo.permissionType = item.permissionType
        this.permissionAddModalDialogShow = true
      }
    },
    beforeDestroy () {
      if (this.permissionOperate === true) {
        sessionStorage.setItem(this.$global.cacheKey.permissionInfosKey, JSON.stringify(this.cachePermissionInfos))
        this.permissionOperate = false
      }
    },
    watch: { //监视
      'permissionInfo.permissionName': {
        deep: true,
        handler: function (newValue, oldValue) {
          this.validatePass.permissionName = newValue.length > 0 && newValue.length < 15
        }
      },
      'permissionInfo.permissionTag': {
        deep: true,
        handler: function (newValue, oldValue) {
          this.validatePass.permissionTag = newValue.length > 0 && newValue.length < 32
        }
      },
      'permissionInfo.permissionType': {
        deep: true,
        handler: function (newValue, oldValue) {
          this.validatePass.permissionType = newValue.length > 0 && newValue.length < 32
        }
      }
    }
  }
</script>

<style scoped>
  .li_style_bottom:not(:last-child) {
    border-bottom: #d6d6d6 1px solid;
  }

  .li_style_height {
    min-height: 32px;
    line-height: 32px;
  }
</style>



