<template>
  <div class="container-fluid">
    <nav class="navbar navbar-inverse navbar-fixed-top">
      <div class="navbar-header">
        <a class="navbar-brand" href="#">Tortoise Tenant Manage System</a>
      </div>
      <div class="collapse navbar-collapse">
        <ul class="nav navbar-nav navbar-right" style="margin-right: 10px">
          <li class="search">
            <input type="text" class="inputNo" placeholder="搜索内容" style="vertical-align: middle">
            <img src="../assets/search.png">
          </li>
          <li><a href="javascript:">{{username}}</a></li>
          <li @click="exitLogin"><a href="javascript:">退出登录</a></li>
        </ul>
      </div>
    </nav>
    <div class="row">
      <ul class="col-sm-2 menu text-center list-unstyled" @click="leftMenuClick">
        <img :src="$global.url.base+headUrl" class="text-center img-circle"
             style="margin-top: 20px;margin-bottom: 10px;min-width: 80px;max-width: 300px;min-height: 80px;max-height: 300px">
        <li type="tenant" :class="{menuLiHover:menuLiActive==='tenant',menuLiLever:menuLiActive!=='tenant'}">
          <img src="../assets/menu_tenant.png" alt="租户管理">
          租户管理
        </li>
        <li type="user" :class="{menuLiHover:menuLiActive==='user',menuLiLever:menuLiActive!=='user'}">
          <img src="../assets/menu_personal.png" alt="用户管理">
          用户管理
        </li>
        <li type="department" :class="{menuLiHover:menuLiActive==='department',menuLiLever:menuLiActive!=='department'}">
          <img src="../assets/menu_department.png" alt="部门管理">
          部门管理
        </li>
        <li type="role"
            :class="{menuLiHover:menuLiActive==='role',menuLiLever:menuLiActive!=='role'}">
          <img src="../assets/menu_role.png" alt="角色管理">
          角色管理
        </li>
        <li type="permission"
            :class="{menuLiHover:menuLiActive==='permission',menuLiLever:menuLiActive!=='permission'}">
          <img src="../assets/menu_permission.png" alt="权限管理">
          权限管理
        </li>
        <li type="personal"
            :class="{menuLiHover:menuLiActive==='personal',menuLiLever:menuLiActive!=='personal'}">
          <img src="../assets/menu_user.png" alt="个人中心">
          个人中心
        </li>
      </ul>
      <div class="col-sm-offset-2 col-sm-10 topNav">
        <!--   路由界面-->
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>

  export default {
    name: 'TortoiseIndex',
    data () {
      return {
        //菜单选择
        menuLiActive: 'tenant',
        //用户名
        username: '用户名',
        //头像
        headUrl: ''
      }
    },
    created () {
      this.menuLiActive = 'tenant'
      this.$router.replace('/index/tenant')
      let object = this.$global.getAuthInfo().object
      this.username = object.tenantName
      this.headUrl = object.headUrl
    },
    mounted () {
    },
    methods: {
      leftMenuClick (event) {
        if (event.target.localName === 'li') {
          this.menuLiActive = event.target.type === undefined ? 'tenant' : event.target.type
          this.$router.replace('/index/' + this.menuLiActive)
        }
      },
      exitLogin () {
        this.$router.replace('/login')
      }
    }
  }
</script>

<style>
  .topNav {
    margin-top: 50px;
  }

  .top15 {
    margin-top: 15px;
  }

  .menu {
    margin: 0;
    position: fixed;
    background-color: #2d2f31;
    top: 50px;
    bottom: 0;
    padding: 0;
  }

  .menu li:hover {
    background-color: #1c1c1c;
    color: white;
  }

  .menu > li {
    cursor: pointer;
    margin-top: 10px;
    height: 48px;
    line-height: 48px;
  }

  .menuLiHover {
    background-color: #1c1c1c;
    color: white;
  }

  .menuLiLever {
    background-color: #282828;
    color: #c2c2c2;
  }

  .menu img {
    width: 18px;
    height: 18px;
    margin-right: 8px;
  }

  .search {
    border: #4a4d50 1px solid;
    border-radius: 6px;
    margin-top: 12px;
    padding-left: 5px;
    margin-right: 30px;
  }

  .search > input {
    border-style: none;
    background-color: #222222;
    color: white;
  }

  .search > img {
    cursor: pointer;
  }

</style>



