<template>
  <!--     提示框 附带动画-->
  <transition name="fade">
    <div id="toast"
         :class="{toast_icon_info_bg:type==='info',toast_icon_warning_bg:type==='warning',toast_icon_success_bg:type==='success',toast_icon_error_bg:type==='error'}">
        <span
          :class="{toast_icon_info:type==='info',toast_icon_warning:type==='warning',toast_icon_success:type==='success',toast_icon_error:type==='error'}"></span>
      <p class="font_3_nowrap">{{msg}}</p>
    </div>
  </transition>
</template>

<script>
  export default {
    name: 'Toast',
    props: {
      type: String,
      msg: String
    }
  }
</script>

<style scoped>

  .font_3_nowrap {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    line-clamp: 3;
    -webkit-box-orient: vertical;
  }

  #toast {
    min-width: 100px;
    width: 200px;
    min-height: 34px;
    max-height: 68px;
    padding: 5px;
    color: #f2dede;
    border-radius: 5px;
    position: fixed;
    bottom: 20px;
    right: 20px;
  }

  #toast > span {
    display: inline-block;
    width: 24px;
    height: 24px;
    margin-right: 5px;
    float: left;
  }

  #toast > p {
    margin-bottom: 0;
  }

  .toast_icon_info {
    background-image: url('data:image/png;base64,');
    background-color: #31708f;
    color: #d9edf7;
    border-color: #bce8f1;
  }

  .toast_icon_info_bg {
    background-color: #31708f;
  }

  .toast_icon_warning {
    background-image: url('data:image/png;base64,');
    background-color: #8a6d3b;
    color: #fcf8e3;
    border-color: #faebcc;
  }

  .toast_icon_warning_bg {
    background-color: #8a6d3b;
  }

  .toast_icon_error {
    background-image: url('data:image/png;base64,');
    background-color: #a94442;
    color: #f2dede;
    border-color: #ebccd1;
  }

  .toast_icon_error_bg {
    background-color: #a94442;
  }

  .toast_icon_success {
    background-image: url('data:image/png;base64,');
    color: #dff0d8;
    background-color: #3c763d;
    border-color: #d6e9c6;
  }

  .toast_icon_success_bg {
    background-color: #3c763d;
  }

  /* 可以设置不同的进入和离开动画,设置持续时间和动画函数 */
  .fade-enter-active, .fade-leave-active {
    transition: opacity 3s
  }

  /* .fade-leave-active, 2.1.8 版本以下 */
  .fade-enter, .fade-leave-to {
    opacity: 0
  }
</style>



