        </li>
        <li v-for="(item,index) in showPageArr" @click="pageClick(item)"
            :class="{active:currentPage!==item,active:currentPage===item}"><a href="#">{{item}}</a></li>
        <li :class="{disabled:currentPage!==totalPage,disabled:currentPage===totalPage}" @click="nextPageClick">
          <a href="#" aria-label="Next">
            <span aria-hidden="true">&raquo;</span>
