        </li>
        <li :class="{disabled:currentPage!==totalPage,disabled:currentPage===totalPage}" @click="lastPageClick"><a
          href="#">尾页</a></li>
      </ul>
    </nav>

    <!--
点击---用户添加
 -->
    <ModalDialog title="用户添加" :close-click="addUserModalCloseClick"
