        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">角色</label>
            <div class="col-sm-9">
              <label class="checkbox-inline" v-for="(item, index) in tenantRoleInfos" :key="index">
                <input type="checkbox" :value="item.id" :disabled="item.id<3" v-model="checkUserRoleIds">
                {{item.roleName}}
              </label>
            </div>
          </div>
        </div>
      </div>
    </ModalDialog>

    <!--
点击---用户详情
-->
    <ModalDialog title="用户详情" :close-click="userDetailModalCloseClick"
                 :sure-click="userDetailModalSureClick" v-show="userDetailModalDialogShow">
      <div slot="modalBody">
        <div class="row">
          <img :src="$global.url.base+userDetail.headUrl" class="text-center img-circle col-sm-3"
               style="margin-top: 10px;min-height: 80px;max-height: 300px">
          <div class="col-sm-8">
            <h3 class="h3">{{userDetail.username}}</h3>
            <p>状态: <span class="text-info">{{userDetail.status===1?'开放':'禁用'}}</span></p>
            <p>公司: {{userDetail.company}}</p>
            <p>号码: {{userDetail.phone}}</p>
            <p>邮箱: {{userDetail.email}}</p>
            <p>角色: {{userDetail.roleNames}}</p>
            <div>权限:
              <table class="table table-striped table-bordered table-hover" v-show="transformPermissions.length>0">
                <thead>
                <tr>
                  <th>类型</th>
                  <th>详情</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(item, index) in transformPermissions" :key="index">
                  <td>{{item.permissionType}}</td>
                  <td>
                    <ol>
                      <li v-for="(item1, index1) in item.permissions" :key="index1">{{item1.permissionName}}</li>
                    </ol>
                  </td>
                </tr>
                </tbody>
              </table>
            </div>
            <p>创建时间: {{userDetail.createDate}}</p>
            <p>更新时间: {{userDetail.updateDate}}</p>
          </div>
        </div>
      </div>
    </ModalDialog>

    <!--
点击---用户修改
-->
    <ModalDialog title="用户修改" :close-click="updateUserModalCloseClick"
                 :sure-click="updateUserModalSureClick" v-show="userUpdateModalDialogShow">
      <div slot="modalBody">
        <div class="form-group top15">
          <div class="row">
            <label class="col-sm-2 text-center">账号</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="user name (长度6~16)"
                     v-model="userUpdateInfo.username">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">密码</label>
