              <input type="password" class="form-control" placeholder="user password (长度8~24)"
                     v-model="userUpdateInfo.password">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">中文名</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="chinese name (长度2~16)"
                     v-model="userUpdateInfo.firstName"
                     minlength="1" maxlength="16">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">英文名</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="english name (长度2~32)"
                     v-model="userUpdateInfo.lastName">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">公司</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="user company (长度2~32)" maxlength="32"
                     v-model="userUpdateInfo.company">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">部门</label>
            <div class="col-sm-9">
              <select class="form-control" v-model="userUpdateInfo.department">
                <option v-for="(item,index) in departments" :key="index">{{item.departmentName}}</option>
              </select>
            </div>
            <span class="text-info small">必选</span>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">职位</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="user company (长度2~16)" maxlength="16"
                     v-model="userUpdateInfo.duty">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">邮箱</label>
            <div class="col-sm-9">
              <input type="email" class="form-control" placeholder="user email" v-model="userUpdateInfo.email">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">电话</label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="user phone" v-model="userUpdateInfo.phone">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">头像</label>
            <div class="col-sm-9">
              <input type="file" class="file-upload" @change="selectUpdateLogo">
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">状态</label>
            <div class="col-sm-9">
              <select class="form-control" v-model="userUpdateInfo.statusValue">
                <option>禁用</option>
                <option>开放</option>
              </select>
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center">角色</label>
            <div class="col-sm-9">
              <label class="checkbox-inline" v-for="(item, index) in tenantRoleInfos" :key="index">
                <input type="checkbox" :value="item.id" :disabled="item.id<3" v-model="checkUserRoleIds">
                {{item.roleName}}
              </label>
            </div>
          </div>
        </div>
      </div>
    </ModalDialog>

  </div>

</template>

<script>

  import ModalDialog from './comment/ModelDialog'

  export default {
    name: 'TortoiseUser',
    components: {
      ModalDialog
    },
    data () {
      return {
        userInfos: [],
        isAdmin: false,
        tenantId: '',
        //分页显示
        showPageArr: [],
        totalPage: 0,
        totalSize: 0,
        currentPage: 1,
        pageSize: 10,
        isFirstPage: true,
        //判断标记
        userAddModalDialogShow: false,
        userDetailModalDialogShow: false,
        userUpdateModalDialogShow: false,
        //添加的用户信息
        userInfo: {
          username: '',
          password: '',
          firstName: '',
          lastName: '',
          company: '',
          department: '',
          duty: '',
          email: null,
          phone: null,
          headFile: null,
          statusValue: '开放',
          status: 1
        },
        //验证条件
        validatePass: {
          username: false,
          password: false,
          firstName: false,
          company: false,
          department: false,
          headFile: false
        },
        //部门
        departments: [],
        selectDepartment: 'ALL',
        //用户详情
        userDetail: {},
        //用户详情---转换 展示 权限信息
        transformPermissions: [],
        transformPermissionMap: new Map(),
        //租户角色信息
        tenantRoleInfos: [],
        //选择用户角色名称的数据
        checkUserRoleIds: [],
        //用户修改的信息
        userUpdateInfo: {},
        //当前点击item的index
        currentUserIndex: 0,
        //查询
        selectSearchFilterField: '用户名',
        showSelectFilterFieldMenu: false,
        inputSearchData: ''
      }
    },
    created () {
      this.isAdmin = this.$global.getAuthInfo().object.roleIds === '1'
      this.tenantId = this.$global.getAuthInfo().object.tenantId
      this.departments = this.$global.getAuthInfo().object.departmentInfos != null ? this.$global.getAuthInfo().object.departmentInfos : []
    },
    mounted () {
      this.getUserInfos(this.tenantId, 1, this.pageSize)
    },
    methods: {
      //选择logo文件
      selectLogo (event) {
        this.userInfo.headFile = event.target.files[0]
        this.validatePass.headFile = /\.(png|jpg|gif)$/i.test(this.userInfo.headFile.name) && this.userInfo.headFile.size < 1024 * 2048
      },
      selectUpdateLogo (event) {
        this.userUpdateInfo.headFile = event.target.files[0]
        this.validatePass.headFile = /\.(png|jpg|gif)$/i.test(this.userUpdateInfo.headFile.name) && this.userUpdateInfo.headFile.size < 1024 * 2048
      },
      //选择部门
      chooseDepartmentClick (department) {
        if (department === 'ALL') {
          department = null
        } else {
          department = this.getDepartmentIdByDepartmentName(department)
        }
        this.getUserInfos(this.tenantId, this.currentPage, this.pageSize, null, null, null, null, null, department)
      },
      //根据部门名称获取部门id
      getDepartmentIdByDepartmentName (departmentName) {
        let departmentId = null
        for (let i = 0; i < this.departments.length; i++) {
          let value = this.departments[i]
          if (value.departmentName === departmentName) {
            departmentId = value.id
            break
          }
        }
        return departmentId
      },
      /**
       * 权限信息转换展示的权限信息
       */
      transformPermissionInfos (permissions) {
        this.transformPermissionMap.clear()
        permissions.forEach(value => {
          let valueArr = this.transformPermissionMap.get(value.permissionType)
          if (valueArr != null) {
            valueArr.push(value)
          } else {
            valueArr = []
            valueArr.push(value)
          }
          this.transformPermissionMap.set(value.permissionType, valueArr)
        })
        this.transformPermissionMap.forEach((value, key) => {
          let permissionObject = {}
          permissionObject.permissionType = key
          permissionObject.permissions = value
          this.transformPermissions.push(permissionObject)
        })
      },
      //查询
      searchFilterClick () {
        this.showSelectFilterFieldMenu = !this.showSelectFilterFieldMenu
      },
      selectFilterFieldClick ($event) {
        this.selectSearchFilterField = $event.target.innerText
        this.showSelectFilterFieldMenu = !this.showSelectFilterFieldMenu
      },
      searchBtnClick () {
        this.currentPage = 1
        switch (this.selectSearchFilterField) {
          case '用户名':
            this.getUserInfos(this.tenantId, this.currentPage, this.pageSize, this.inputSearchData, null)
            break
          case '中文名':
            this.getUserInfos(this.tenantId, this.currentPage, this.pageSize, null, this.inputSearchData, null)
            break
          case '英文名':
            this.getUserInfos(this.tenantId, this.currentPage, this.pageSize, null, null, this.inputSearchData, null)
            break
          default:
            this.getUserInfos(this.tenantId, this.currentPage, this.pageSize)
            break
        }
      },
      //获取用户信息
      getUserInfos (tenantId, page, pageSize, username, firstName, lastName, company, duty, departmentId) {
        this.$axios.get(this.$global.url.user.get, {
          params: {
            tenantId, page, pageSize, username, firstName, lastName, company, duty, departmentId
          }
        })
          .then(response => {
            if (response !== null) {
              this.userInfos = response.data.data
              this.totalPage = response.data.totalPage
              this.totalSize = response.data.totalSize
              this.getShowPageArr()
            }
          })
      },
      //首页
      firstPageClick () {
        if (this.currentPage === 1) {
          return
        }
        this.currentPage = 1
        this.showPageArr = []
        this.getUserInfos(this.tenantId, this.currentPage, this.pageSize)
        this.getShowPageArr()
      },
      //最后一页
      lastPageClick () {
        if (this.currentPage === this.totalPage) {
          return
        }
        let maxPageSize = parseInt(this.totalSize / this.pageSize) + 1
        this.currentPage = maxPageSize
        if (maxPageSize > 5) {
          this.showPageArr = []
          for (let i = maxPageSize - 4; i <= maxPageSize; i++) {
            this.showPageArr.push(i)
          }
        } else {
          this.getShowPageArr()
        }
        this.getUserInfos(this.tenantId, this.currentPage, this.pageSize)
      },
      //上一页
      prePageClick () {
        if (this.currentPage === 1) {
          return
        }
        let showPageArrFirstElement = this.showPageArr[0]
        if (showPageArrFirstElement > 1 && this.currentPage === showPageArrFirstElement) {
          this.showPageArr = this.showPageArr.map(value => value - 1)
        }
        if (this.currentPage > 1) {
          this.currentPage--
          this.getUserInfos(this.tenantId, this.currentPage, this.pageSize)
        }
      },
      //下一页
      nextPageClick () {
        if (this.currentPage === this.totalPage) {
          return
        }
        let maxPageSize = this.totalSize / this.pageSize
        let showPageArrLastElement = this.showPageArr[this.showPageArr.length - 1]
        if (showPageArrLastElement < maxPageSize && this.currentPage === showPageArrLastElement) {
          this.showPageArr = this.showPageArr.map(value => value + 1)
        }
        if (this.currentPage < maxPageSize) {
          this.currentPage++
          this.getUserInfos(this.tenantId, this.currentPage, this.pageSize)
        }
      },
      //分页item
      pageClick (item) {
        this.currentPage = item
        this.getUserInfos(this.tenantId, item, this.pageSize)
      },
      //获取分页数组
      getShowPageArr () {
        this.showPageArr = []
        let maxPageSize = this.totalSize / this.pageSize + 1
        if (maxPageSize > 5) {
          for (let i = 1; i <= 5; i++) {
            this.showPageArr.push(i)
          }
        } else {
          for (let i = 1; i < maxPageSize; i++) {
            this.showPageArr.push(i)
          }
        }
      },
      /**
       * 用户添加
       */
      addUserClick () {
        //初始化数据
        this.userInfo = {
          username: '',
          password: '',
          firstName: '',
          lastName: '',
          company: '',
          department: '',
          duty: '',
          email: null,
          phone: null,
          headFile: null,
          statusValue: '开放',
          status: 1
        }
        this.userInfo.company = this.$global.getAuthInfo().object.company
        this.checkUserRoleIds = []
        //获取定义的角色
        if (this.tenantRoleInfos.length === 0) {
          if (this.$global.getRoleInfos() == null) {
            this.$axios.get(this.$global.url.role.get)
              .then(response => {
                if (response !== null) {
                  this.tenantRoleInfos = response.data.data
                  //缓存角色信息
                  sessionStorage.setItem(this.$global.cacheKey.roleInfosKey, JSON.stringify(response.data.data))
                }
              })
          } else {
            this.tenantRoleInfos = this.$global.getRoleInfos()
          }
        }
        //部门下拉选择默认选择第一个
        if (this.departments.length > 0) {
          this.userInfo.department = this.departments[0].departmentName
        }
        this.userAddModalDialogShow = true
      },
      addUserModalCloseClick () {
        this.userAddModalDialogShow = false
      },
      addUserModalSureClick () {
        this.userInfo.status = this.userInfo.statusValue === '开放' ? 1 : 0
        if (this.validatePass.username && this.validatePass.password && this.validatePass.firstName
          && this.validatePass.company && this.validatePass.department) {
          let d = new Date()
          let currentDate = d.getFullYear() + '-' + d.getMonth() + '-' + d.getDate() + ' ' + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds()
          this.userAddModalDialogShow = false
          if (this.checkUserRoleIds.length > 0) {
            let roleIds = ''
            this.checkUserRoleIds.forEach(value => roleIds += value + ',')
            this.userInfo.roleIds = roleIds
          } else {
            this.userInfo.roleIds = null
          }
          let formData = new FormData()
          formData.append('username', this.userInfo.username)
          formData.append('password', this.userInfo.password)
          formData.append('firstName', this.userInfo.firstName)
          formData.append('lastName', this.userInfo.lastName)
          formData.append('company', this.userInfo.company)
          formData.append('departmentId', this.getDepartmentIdByDepartmentName(this.userInfo.department))
          formData.append('duty', this.userInfo.duty)
          if (this.userInfo.email != null && this.userInfo.email.length > 0) {
            formData.append('email', this.userInfo.email)
          }
          if (this.userInfo.phone != null && this.userInfo.phone.length > 0) {
            formData.append('phone', this.userInfo.phone)
          }
          if (this.userInfo.roleIds != null && this.userInfo.roleIds.length > 0) {
            formData.append('roleIds', this.userInfo.roleIds)
          }
          if (this.validatePass.headFile) {
            formData.append('headFile', this.userInfo.headFile)
          }
          formData.append('status', this.userInfo.status)
          this.$axios.post(this.$global.url.user.add, formData)
            .then(response => {
              if (response !== null) {
                let userInfo = response.data.data
                userInfo.updateDate = currentDate
                userInfo.createDate = currentDate
                this.userInfos.push(userInfo)
                //提示
                this.$pubSub.publish('toastShow', {
                  type: 'success',
                  msg: '添加用户信息成功'
                })
              }
            })
        } else {
          this.$pubSub.publish('toastShow', {
            type: 'warning',
            msg: '请按要求填写必要信息,谢谢!!'
          })
        }
      },
      /**
       * 用户详情
       */
      userDetailModalCloseClick () {
        this.userDetailModalDialogShow = false
      },
      userDetailModalSureClick () {
        this.userDetailModalDialogShow = false
      },
      /**
       * 用户修改
       */
      updateUserModalCloseClick () {
        this.userUpdateModalDialogShow = false
      },
      updateUserModalSureClick () {
        let d = new Date()
        let currentDate = d.getFullYear() + '-' + d.getMonth() + '-' + d.getDate() + ' ' + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds()
        this.userUpdateInfo.status = this.userUpdateInfo.statusValue === '开放' ? 1 : 0
        this.userUpdateModalDialogShow = false
        let formData = new FormData()
        formData.append('id', this.userUpdateInfo.id)
        formData.append('username', this.userUpdateInfo.username)
        if (this.userUpdateInfo.password != null && this.userUpdateInfo.password.length > 7) {
          formData.append('password', this.userUpdateInfo.password)
        }
        formData.append('firstName', this.userUpdateInfo.firstName)
        formData.append('lastName', this.userUpdateInfo.lastName)
        formData.append('company', this.userUpdateInfo.company)
        formData.append('departmentId', this.getDepartmentIdByDepartmentName(this.userUpdateInfo.department))
        formData.append('duty', this.userUpdateInfo.duty)
        if (this.userUpdateInfo.email != null && this.userUpdateInfo.email.length > 0) {
          formData.append('email', this.userUpdateInfo.email)
        }
        if (this.userUpdateInfo.phone != null && this.userUpdateInfo.phone.length > 0) {
          formData.append('phone', this.userUpdateInfo.phone)
        }
        if (this.checkUserRoleIds.length > 0) {
          let roleIds = ''
          this.checkUserRoleIds.forEach(value => roleIds += value + ',')
          formData.append('roleIds', roleIds)
        }
        if (this.validatePass.headFile) {
          formData.append('headFile', this.userUpdateInfo.headFile)
        }
        formData.append('status', this.userUpdateInfo.status)
        this.$axios.put(this.$global.url.user.update, formData)
          .then(response => {
            if (response !== null) {
              let userInfo = response.data.data
              userInfo.updateDate = currentDate
              //更新当前显示租户信息
              this.userInfos.splice(this.currentUserIndex, 1, userInfo)
              //提示
              this.$pubSub.publish('toastShow', {
                type: 'success',
                msg: '修改用户信息成功'
              })
            }
          })
      },
      /**
       * 用户每个item的点击
       */
      delegateAppItemClick (e, item, index) {
        switch (e.target.innerText) {
          case '删除':
            this.$axios.delete(this.$global.url.user.delete, {
              params: {
                id: item.id
              }
            })
              .then(response => {
                if (response !== null) {
                  this.userInfos.splice(index, 1)
                  //提示
                  this.$pubSub.publish('toastShow', {
                    type: 'success',
                    msg: '用户删除成功'
                  })
                }
              })
            break
          case '详情':
            this.transformPermissions = []
            if (item.roleInfos) {
              let roleNames = ''
              let permissionNames = []
              for (let i = 0; i < item.roleInfos.length; i++) {
                let roleInfo = item.roleInfos[i]
                roleNames += i + ':' + roleInfo.roleName + ' '
                if (roleInfo.permissionInfos != null && roleInfo.permissionInfos.length > 0) {
                  roleInfo.permissionInfos.forEach(permission => {
                    permissionNames.push(permission)
                  })
                }
              }
              item.roleNames = roleNames
              this.transformPermissionInfos(permissionNames)
            }
            this.userDetail = item
            this.userDetailModalDialogShow = true
            break
          case '修改':
            //给选择的租户id赋值
            this.checkUserRoleIds = []
            if (item.roleIds.length > 0) {
              item.roleIds.split(',').forEach(value => this.checkUserRoleIds.push(value))
            }
            item.statusValue = item.status === 1 ? '开放' : '禁用'
            this.userUpdateInfo = item
            //获取所有租户定义的角色
            if (this.tenantRoleInfos.length === 0) {
              if (this.$global.getRoleInfos() == null) {
                this.$axios.get(this.$global.url.role.get)
                  .then(response => {
                    if (response !== null) {
                      this.tenantRoleInfos = response.data.data
                      //缓存角色信息
                      sessionStorage.setItem(this.$global.cacheKey.roleInfosKey, JSON.stringify(response.data.data))
                    }
                  })
              } else {
                this.tenantRoleInfos = this.$global.getRoleInfos()
              }
            }
            //部门下拉选择默认选择已有的
            if (this.departments.length > 0) {
              this.userUpdateInfo.department = item.departmentInfo.departmentName
            }
            this.currentUserIndex = index
            this.userUpdateModalDialogShow = true
            break
          default:
            break
        }
      }
    },
    watch: { //监视
      'userInfo.username': {
        deep: true,
        handler: function (newValue, oldValue) {
          this.validatePass.username = newValue.length > 5 && newValue.length < 15
        }
      },
      'userInfo.password': {
        deep: true,
        handler: function (newValue, oldValue) {
          this.validatePass.password = newValue.length > 7 && newValue.length < 19
        }
      },
      'userInfo.firstName': {
        deep: true,
        handler: function (newValue, oldValue) {
          this.validatePass.firstName = newValue.length > 0 && newValue.length < 16
        }
      },
      'userInfo.company': {
        deep: true,
        handler: function (newValue, oldValue) {
          this.validatePass.company = newValue.length > 1 && newValue.length < 32
        }
      },
      'userInfo.department': {
        deep: true,
        handler: function (newValue, oldValue) {
          this.validatePass.department = newValue.length > 0 && newValue.length < 32
        }
      }
    }
  }
</script>

<style scoped>
  .dropdown_menu_show {
    display: block;
  }

  .dropdown_menu_hide {
    display: none;
  }
</style>



