<template>

</template>

<script>

  const url = {
    base: 'http://10.124.132.49:8088/tortoise',
    tenant: {
      add: '/tenant/add',
      delete: '/tenant/delete',
      get: '/tenant/get',
      update: '/tenant/update',
      updateTenantStatus: '/tenant/updateTenantStatus',
      updatePersonal: '/tenant/updatePersonal',
      auth: '/tenant/auth',
      authMultipoint: '/tenant/authMultipoint'
    },
    user: {
      add: '/user/add',
      delete: '/user/delete',
      get: '/user/get',
      update: '/user/update',
      auth: '/user/auth',
      authMultipoint: '/user/authMultipoint'
    },
    role: {
      add: '/role/add',
      delete: '/role/delete',
      get: '/role/get',
      update: '/role/update'
    },
    permission: {
      add: '/permission/add',
      delete: '/permission/delete',
      get: '/permission/get',
      update: '/permission/update'
    },
    department:{
      add: '/department/add',
      delete: '/department/delete',
      get: '/department/get',
      update: '/department/update'
    }
  }
  const cacheKey = {
    authInfoKey: 'authInfoKey',
    tenantInfosKey: 'tenantInfosKey',
    roleInfosKey: 'roleInfosKey',
    permissionInfosKey: 'permissionInfosKey'
  }
  //认证信息
  let authInfo = null
  //租户信息
  let tenantInfos = null
  //角色信息
  let roleInfos = null
  //权限信息
  let permissionInfos = null
  export default {
    url,
    cacheKey,
    getAuthInfo () {
      if (authInfo != null) {
        return authInfo
      } else {
        let authInfoJson = sessionStorage.getItem(cacheKey.authInfoKey)
        if (authInfoJson != null) authInfo = JSON.parse(authInfoJson)
        return authInfo
      }
    },
    getTenantInfos () {
      if (tenantInfos != null) {
        return tenantInfos
      } else {
        let tenantInfosJson = sessionStorage.getItem(cacheKey.tenantInfosKey)
        if (tenantInfosJson != null) tenantInfos = JSON.parse(tenantInfosJson)
        return tenantInfos
      }
    },
    getRoleInfos () {
      if (roleInfos != null) {
        return roleInfos
      } else {
        let roleInfosJson = sessionStorage.getItem(cacheKey.roleInfosKey)
        if (roleInfosJson != null) roleInfos = JSON.parse(roleInfosJson)
        return roleInfos
      }
    },
    getPermissionInfos () {
      if (permissionInfos != null) {
        return permissionInfos
      } else {
        let permissionInfosJson = sessionStorage.getItem(cacheKey.permissionInfosKey)
        if (permissionInfosJson != null) permissionInfos = JSON.parse(permissionInfosJson)
        return permissionInfos
      }
    },
    destroy () {
      authInfo = null
      tenantInfos = null
      roleInfos = null
      permissionInfos = null
      sessionStorage.removeItem(cacheKey.authInfoKey)
      sessionStorage.removeItem(cacheKey.tenantInfosKey)
      sessionStorage.removeItem(cacheKey.roleInfosKey)
      sessionStorage.removeItem(cacheKey.permissionInfosKey)
      sessionStorage.clear()
    }
  }
</script>

<style scoped>

</style>



